import React, {useEffect} from "react";
import {useSelector} from "react-redux";
import {useGetContent} from "../../store/action";
import Prism from 'prismjs';
import 'prismjs/components/prism-markup-templating';
import 'prismjs/plugins/autoloader/prism-autoloader';
import {getFileExt} from "../../hooks/ext";
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/components/prism-php.min';
import 'prismjs/components/prism-javascript.min';
import 'prismjs/components/prism-css.min';
import 'prismjs/components/prism-textile.min';
import {useGetCurrent, useGetQuery} from "../../hooks";

function Code() {
  const {f} = useGetQuery();
  const {loading, content} = useSelector(state => state);
  const getContent = useGetContent();
  const {id, name} = useGetCurrent(f);

  useEffect(() => {
    if(id)  getContent(id);
  }, [id]);

  const language = getFileExt(name);
  const html = content&&language!=='txt' ? Prism.highlight(content, Prism.languages[language], language) : content;

  return (
    <pre className={`code-content language-${language}`}>
      {
        loading ? '加载中，请稍后' : <code
          className={`language-${language}`}
          dangerouslySetInnerHTML={{__html: html}}
        />
      }
    </pre>
  )
}

export default Code;












