import {Layout} from 'antd';
import React, {useEffect} from "react";
import {useGetMessage} from "./store/action";
import {Route, Switch} from "react-router-dom";
import 'antd/dist/antd.css';
import HeaderNav from "./components/header/header";
import IndexPage from "./views/index/IndexPage";
import CodePage from "./views/content/CodePage";
import MediaPage from "./views/media/MediaPage";
import MarkdownPage from "./views/content/MarkdownPage";
import Page404 from "./views/404/Page404";

function App() {
  const getMessage = useGetMessage();
  useEffect(() => {
    getMessage();
  }, [getMessage])
  return <Layout>
    <HeaderNav />
    <Layout>
      <Switch>
        <Route path={'/'} exact render={() => <IndexPage/>}/>
        <Route path={'/code'} exact render={() => <CodePage/>}/>
        <Route path={'/video'} exact render={() => <MediaPage/>}/>
        <Route path={'/audio'} exact render={() => <MediaPage/>}/>
        <Route path={'/md'} exact render={() => <MarkdownPage/>}/>
        <Route path={''} render={() => <Page404/>}/>
      </Switch>
    </Layout>
  </Layout>
}

export default App;