import React from "react";
import {useGetFiles} from "../../hooks";
import {List} from "antd";
import File from "../../components/file";

function Main() {
  const files = useGetFiles();
  return (
    <List
      grid={{
        gutter: 16,
        xs: 1,
        sm: 2,
        md: 4,
        lg: 4,
        xl: 6,
        xxl: 6,
      }}
      dataSource={files}
      renderItem={(file) => <File file={file}/>}
    />
  );
}

export default Main;