import React, {Fragment} from 'react';
import {useCurrent} from "../../store/action";
import {useGetMenu, useGetQuery} from "../../hooks";
import {Layout, List} from "antd";
import {FolderOpenOutlined, FolderOutlined} from "@ant-design/icons";

const {Sider} = Layout;

function SiderMenu() {
  const {d} = useGetQuery();
  const current = useCurrent();
  const menuData = useGetMenu();
  let rootMenu = menuData.filter(item => !item.pid);
  const getChild = id => {
    return menuData.filter(item => item.pid === id);
  }
  const renderMenu = (menu, level = 1) => {
    if (!menu.length) return '';
    return menu.map(item => {
      const {id, name, open} = item;
      const child = getChild(id);
      return <Fragment key={Math.random()}>
        <List.Item
          className={d && id.endsWith(d) ? 'active-menu' : ''}
          key={id}
          style={{paddingLeft: `${level * 20 + 16}px`}}
          onClick={() => current(id)}
        >
          {open ? <FolderOpenOutlined style={{paddingRight: '5px'}}/> : <FolderOutlined style={{paddingRight: '5px'}}/>}
          {name}
        </List.Item>
        {open && child !== undefined && renderMenu(child, level + 1)}
      </Fragment>
    })
  }

  return (
    <Sider theme="light" width={300} className="site-layout-background">
      <List
        size={'small'}
        header={
          <div
            style={{paddingLeft: '16px'}}
            onClick={() => current(0)}
          >
            {<FolderOpenOutlined style={{paddingRight: '5px'}}/>}
            {'所有文件'}
          </div>
        }
      >
        {renderMenu(rootMenu)}
      </List>
    </Sider>
  )
}

export default SiderMenu;