import React, {Fragment} from "react";
import {useGetCurrent, useGetParents, useGetQuery} from "../../hooks";
import {useCheckAll, useCurrent} from "../../store/action";
import {Breadcrumb, Checkbox} from "antd";
import { HomeOutlined, FolderOpenOutlined, FileOutlined } from '@ant-design/icons';
import {useSelector} from "react-redux";

function BreadMenu() {
  const checkAll = useCheckAll()
  const current = useCurrent()
  const parents = useGetParents();
  const {data} = useSelector(state=>state);
  const {d, f} = useGetQuery();
  const {id: mid=0, name: menuName} = useGetCurrent(d);
  const {id: fid, name: fileName} = useGetCurrent(f);
  if(fid && mid)  parents.unshift({id: mid, name: menuName});
  const name = fid ? fileName : menuName;
  const checked = data.filter(item=>item.pid===mid).every(item=>item.checked);

  return (<Fragment>

    <Breadcrumb style={{padding: '12px'}}>
      <Breadcrumb.Item key={'checkAll'}>
        <Checkbox checked={checked} onClick={e=>e.stopPropagation()}
                  onChange={({target: {checked}})=>{
                    checkAll(checked, mid);
                  }}>全选</Checkbox>
      </Breadcrumb.Item>
      <Breadcrumb.Item key={'first'}>
        <HomeOutlined onClick={() => current(0)} />
      </Breadcrumb.Item>
      {
        parents.reverse().map(({id, name}) =>
          <Breadcrumb.Item style={{cursor: 'pointer'}} key={id} onClick={() => current(id)}>
            <FolderOpenOutlined style={{paddingRight: '5px'}} />
            {name}
          </Breadcrumb.Item>
        )
      }
      { name ? (<Breadcrumb.Item key={'last'}>{ fid ? <FileOutlined style={{marginRight: '5px'}} /> : <FolderOpenOutlined style={{marginRight: '5px'}}/>}{name}</Breadcrumb.Item>) : '' }
    </Breadcrumb>
    </Fragment>
  );
}

export default BreadMenu;











