import { createStore } from "redux";

const reducer = (state = {
  data: [],
  loading: false,
  confirm: false,
  visible: false,
  footer: false,
  upload: '',
  modal: {},
  modalType: '',
}, action) => {
  switch (action.type){
    case 'LOADING':
      return {
        ...state,
        loading: true,
      }
    case 'COMPLETE':
      return {
        ...state,
        data: action.data
      }
    case 'CONTENT':
      return {
        ...state,
        loading: false,
        content: action.content
      }
    case 'MODAL_VISIBLE':
      return {
        ...state,
        visible: action.visible,
        footer: action.footer
      }
    case 'SET_MODAL':
      return {
        ...state,
        modal: action.modal,
        modalType: action.modalType
      }
    case 'CONFIRM':
      return {
        ...state,
        confirm: action.confirm
      }
    case 'MESSAGE':
      return {
        ...state,
        msg: action.msg
      }
    case 'UPLOAD':
      return {
        ...state,
        upload: action.upload
      }
    default:
      return state;
  }
};

const store = createStore(reducer);

export default store;
