import React from "react";
import {Modal, Input, Upload, Breadcrumb} from 'antd';
import {useSelector} from "react-redux";
import {FolderOpenOutlined, HomeOutlined, InboxOutlined} from '@ant-design/icons';
import {useCurrent, useCustomUpload, useGetUploadScript} from "../store/action";
import {useGetCurrent, useGetParents, useGetQuery, useShowMessage} from "../hooks";

const {Dragger} = Upload;


function ShowModal(props) {
  const {footer, modal, modalType} = useSelector(state => state);
  const {InputRef, handleOk, handleCancel} = props;
  const getUploadScript = useGetUploadScript();
  const customUpload = useCustomUpload();
  const current = useCurrent()
  const parents = useGetParents();
  const {d} = useGetQuery();
  const {id='root', name} = useGetCurrent(d);
  const showMessage = useShowMessage();
  let uploadUrl = '';

  const showFooter = {};
  if (!footer) showFooter.footer = null;

  const options = {
    name: 'file',
    multiple: true,
    beforeUpload: async (file) => {
      const {code, data, msg} = await getUploadScript(id, file);
      if (Number(code) === 200) {
        uploadUrl = data;
      }else{
        showMessage(msg, false);
      }
    },
    customRequest: async ({
                            action,
                            data,
                            file,
                            filename,
                            headers,
                            onError,
                            onProgress,
                            onSuccess,
                            withCredentials,
                          }) => {
      customUpload(uploadUrl, file, onProgress, onSuccess);
    }
  };

  return <Modal
    {...modal}
    onOk={handleOk}
    onCancel={handleCancel}
    destroyOnClose={true}
    maskClosable={true}
  >
    <Breadcrumb style={{padding: '12px'}}>
      <Breadcrumb.Item key={'first'}>
        <span style={{fontWeight: 600, color: '#000000D9'}}>当前目录：</span>
      </Breadcrumb.Item>
      <Breadcrumb.Item key={'first'}>
        <HomeOutlined onClick={() => current(0)} />
      </Breadcrumb.Item>
      {
        parents.reverse().map(({id, name}) =>
          <Breadcrumb.Item style={{cursor: 'pointer'}} key={id} onClick={() => current(id)}>
            <FolderOpenOutlined style={{paddingRight: '5px'}} />
            {name}
          </Breadcrumb.Item>
        )
      }
      <Breadcrumb.Item key={'last'}>
        <FolderOpenOutlined style={{marginRight: '5px'}}/>{name}
      </Breadcrumb.Item>
      <Breadcrumb.Item key={'hide'}>&nbsp;</Breadcrumb.Item>
    </Breadcrumb>

    {modalType==='create' && <Input ref={InputRef} />}
    {modalType==='upload' && <Dragger{...options}>
      <p className="ant-upload-drag-icon">
        <InboxOutlined/>
      </p>
      <p className="ant-upload-text">Click or drag file to this area to upload</p>
      <p className="ant-upload-hint">
        Support for a single or bulk upload. Strictly prohibit from uploading company data or other
        band files
      </p>
    </Dragger>}
  </Modal>
}

export default ShowModal;