const extData = {
  images: [
    'bmp', 'jpg', 'jpeg', 'png', 'gif'
  ],
  video: [
    'mp4', 'webm', 'mkv', 'avi', 'flv',
    'mpeg', 'mpg', 'rm', 'rmvb', 'mov', 'wmv',
  ],
  audio: [
    'ogg', 'mp3', 'wav'
  ],
  code: [
    'txt', 'html', 'htm', 'php', 'css', 'go', 'java',
    'js', 'javascript', 'json', 'txt', 'sh'
  ],
  md: [
    'md', 'markdown'
  ],
  doc: [
    'csv', 'doc', 'docx', 'odp', 'ods',
    'odt', 'pot', 'potm', 'potx', 'pps',
    'ppsx', 'ppsxm', 'ppt', 'pptm', 'pptx',
    'rtf', 'xls', 'xlsx',
    'pdf', 'rar', 'zip'
  ]
};

const getFileExt = name => {
  return name ? name.slice(name.lastIndexOf('.') + 1) : 'javascript';
}

const getFileType = name => {
  const ext = getFileExt(name);
  for (let key in extData) {
    if (extData[key].includes(ext)) return key;
  }
}

export {
  getFileExt,
  getFileType
};