import React, {useEffect} from "react";
import {useSelector} from "react-redux";
import {useGetContent} from "../../store/action";
import marked from "marked";
import hljs from 'highlight.js';
import 'highlight.js/styles/dark.css';
import {useGetCurrent, useGetQuery} from "../../hooks";

const options = {
  renderer: new marked.Renderer(),
  highlight: function (code, language) {
    const validLanguage = hljs.getLanguage(language) ? language : 'plaintext';
    return hljs.highlight(validLanguage, code).value;
  },
  pedantic: false,
  gfm: true,
  breaks: false,
  sanitize: false,
  smartLists: true,
  smartypants: false,
  xhtml: false
}
marked.setOptions(options);

function Markdown() {
  const {f} = useGetQuery();
  const {content} = useSelector(state => state);
  const getContent = useGetContent();
  const {id} = useGetCurrent(f);

  useEffect(() => {
    if(id)  getContent(id);
  }, [id]);

  const html = content ? marked(content) : '加载中，请稍后';

  return <div className={'md_container'} dangerouslySetInnerHTML={{__html: html}}/>
}

export default Markdown;












