import React, {useRef} from "react";
import {useCheckFile, useCurrent, useDeleteItem, useSetFile} from "../store/action";
import {Button, Card, List, Checkbox, Modal} from "antd";
import {getFileType} from "../hooks/ext";
import {
  DownloadOutlined,
  FileTextOutlined,
  FolderOpenOutlined,
  DeleteOutlined,
  ExclamationCircleOutlined
} from "@ant-design/icons";

const {confirm} = Modal;

function File(props) {
  const deleteItem = useDeleteItem();
  const current = useCurrent();
  const setFile = useSetFile();
  const checkFile = useCheckFile();
  const {file: {id, name, folder, checked}} = props;

  const show = () => {
    if (folder) return false;
    return getFileType(name) === 'doc';
  }
  const downloadRef = useRef();
  const deleteRef = useRef();

  const handleClick = () => {
    if (folder) {
      current(id);
    } else {
      !show() && setFile(id);
    }
  }

  const handleMouseEnter = () => {
    const {current: download} = downloadRef;
    const {current: del} = deleteRef;
    if (del) del.style.display = 'block';
    if (download) download.style.display = 'block';
  }
  const handleMouseLeave = () => {
    const {current: download} = downloadRef;
    const {current: del} = deleteRef;
    if (del) del.style.display = 'none';
    if (download) download.style.display = 'none';
  }
  const handleDownload = () => {
    console.log(id);
  }
  const handleDelete = (resolve, reject) => {
    deleteItem(resolve, reject, id);
  }

  const showDeleteConfirm = (folder) => {
    confirm({
      title: '确定要删除当前文件' + (folder ? '夹' : '') + '?',
      icon: <ExclamationCircleOutlined/>,
      okText: '删除',
      okType: 'danger',
      cancelText: '取消',
      onOk() {
        return new Promise((resolve, reject) => {
          handleDelete(resolve, reject)
        }).catch(() => console.log('出错!'))
      }
    });
  }

  return <List.Item>
    <Card
      className={'folder-item'}
      onClick={handleClick}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      {folder ?
        <FolderOpenOutlined className={'file-icon'}/> :
        <FileTextOutlined className={'file-icon'}/>}

      <div className={'ant-card-body-title'}>{name}</div>
      <Button
        danger
        className={'delete-btn'}
        ref={deleteRef}
        type="primary"
        size="small"
        icon={<DeleteOutlined/>}
        onClick={e => {
          e.stopPropagation();
          showDeleteConfirm(folder);
        }}
      >删除</Button>
      {show() &&
      <Button
        className={'download-btn'}
        ref={downloadRef}
        type="primary"
        size="small"
        icon={<DownloadOutlined/>}
        onClick={e => {
          e.stopPropagation();
          handleDownload(id);
        }}
      >下载</Button>}
      <Checkbox
        className={'file-check'}
        checked={checked}
        onClick={e => e.stopPropagation()}
        onChange={({target: {checked}}) => {
          checkFile(checked, id);
        }}/>
    </Card>
  </List.Item>
}

export default File;












