import React, {useEffect} from "react";
import {useSelector} from "react-redux";
import {useGetContent} from "../../store/action";
import {getFileType} from "../../hooks/ext";
import {useGetCurrent, useGetQuery} from "../../hooks";

function Video() {
  const {f} = useGetQuery();
  const getContent = useGetContent();
  const {content} = useSelector(state => state);
  const {id, name} = useGetCurrent(f);
  const type = getFileType(name);
  useEffect(() => {
    if(id)  getContent(id);
  }, [id]);

  return <div id={'video_box'}>
    {
      type === 'video' ?
        <video id={'video'} controls autoPlay src={content}/> :
        <audio id={'audio'} controls autoPlay src={content}/>
    }

  </div>
}

export default Video;












