import {useDispatch, useSelector} from "react-redux";
import http from "./http";
import {useHistory} from 'react-router-dom';
import {getFileType} from "../hooks/ext";
import {useGetCurrent, useGetQuery, useSetModal, useShowMessage} from "../hooks";

const useGetMessage = () => {
  const dispatch = useDispatch();
  return () => {
    dispatch({
      type: 'LOADING'
    });
    http.get('/api')
      .then(({data: {data}}) => {
        data.forEach(item => item.checked = false);
        dispatch({
          type: 'COMPLETE',
          data
        })
      });
  }
}

const useGetContent = () => {
  const dispatch = useDispatch();
  return (id) => {
    dispatch({
      type: 'LOADING',
    });
    http.get('/api/content/' + id)
      .then(({data: {code, data}}) => {
        if (Number(code) === 200) {
          const {data: content} = data;
          dispatch({
            type: 'CONTENT',
            content
          })
        }
      });
  }
}

const useCreateFolder = () => {
  const showMessage = useShowMessage();
  const getMessage = useGetMessage();
  const setModel = useSetModal();
  const {d} = useGetQuery();
  const {id = 'root'} = useGetCurrent(d);
  return (name) => {
    setModel({confirmLoading: true});
    http.post('/api/create', {id, name})
      .then(({data: {code, msg}}) => {
        let success = false;
        if (Number(code) === 201) {
          getMessage();
          success = true;
          setModel({confirmLoading: false, visible: false})
        }else{
          setModel({confirmLoading: false})
        }
        showMessage(msg, success);
      });
  }
}

const getCheckedIds = data => {
  const ret = [];
  data.forEach(item => {
    if (item.checked) ret.push(item.id);
  });
  return ret;
}

const useDeleteItem = () => {
  const showMessage = useShowMessage();
  const getMessage = useGetMessage();
  const data = useSelector(state => state.data);
  return (resolve, reject, id = '') => {
    const postData = id ? [id] : getCheckedIds(data);
    http.post('/api/delete', {ids: postData})
      .then(({data: {code, msg}}) => {
        resolve();
        let success = false;
        if (Number(code) === 204) {
          getMessage();
          success = true;
        }
        showMessage(msg, success);
      });
  }
}

const useGetUploadScript = () => {
  return async (id, file) => {
    const {size, name} = file;
    const {data} = await http.post('/api/upload', {id, size, name});
    return data;
  }
}

const useCustomUpload = () => {
  const range = 10485760;
  const reader = new FileReader();
  return (url, file, progress, success) => {
    reader.readAsArrayBuffer(file);
    reader.onloadend = async ({target: {result}})=>{
      let start = 0, end = Math.min(range, file.size), res, chunk = 0;
      while(start < file.size){
        const config = {
          headers: {
            'Content-Type': 'application/json',
            'Content-Range': `bytes ${start}-${end - 1}/${file.size}`
          },
          onUploadProgress: ({ total, loaded }) => {
            loaded += range * chunk;
            console.log(file.size, loaded)
            progress({ percent: Math.round((loaded / file.size) * 100).toFixed(2) }, file);
          }
        };
        let data = result.slice(start, end);
        res = await http.put(url, data, config);
        console.log(res);
        start = end;
        end = Math.min(file.size, start + range);
        chunk++;
      }
      console.log(res);
      success(res, file);
      // return res;
    }
  }
}

const useSetFile = () => {
  const data = useSelector(state => state.data);
  const history = useHistory();
  const {d} = useGetQuery();
  return (id) => {
    const file = data.find(item => item.id === id);
    let fileType = getFileType(file.name);
    const url = `/${fileType}` + (d ? '?d=' + d + '&f=' : '?f=') + id.slice(-8);
    history.push(url);
  }
}

const useCheckFile = () => {
  const dispatch = useDispatch();
  const data = useSelector(state => state.data);
  return (val, id) => {
    if (id) {
      data.forEach(item => item.id === id ? item.checked = val : '');
    } else {
      data.forEach(item => item.checked = val);
    }
    dispatch({
      type: 'COMPLETE',
      data
    })
  }
}

const useCheckAll = () => {
  const dispatch = useDispatch();
  const data = useSelector(state => state.data);
  return (val, pid) => {
    data.forEach(item => item.pid === pid ? item.checked = val : '');
    dispatch({
      type: 'COMPLETE',
      data
    })
  }
}

const useCurrent = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const data = useSelector(state => state.data);
  return (id) => {
    data.forEach(item => item.checked = false);
    dispatch({
      type: 'COMPLETE',
      data
    })
    const url = id ? `/?d=${id.slice(-8)}` : '/';
    history.push(url);
  }
}

export {
  useSetFile,
  useCreateFolder,
  useDeleteItem,
  useCheckFile,
  useCheckAll,
  useGetMessage,
  useCurrent,
  useGetContent,
  useGetUploadScript,
  useCustomUpload
}
