import {useDispatch, useSelector} from "react-redux";
import {useHistory} from "react-router-dom";
import qs from "qs";
import {message} from "antd";

const useGetFiles = () => {
  const {d} = useGetQuery();
  const {id=0} = useGetCurrent(d);
  const {data} = useSelector(state => state);
  if(!data.length)  return [];
  return data.filter(item => item.pid === id);
}

const useGetMenu = () => {
  const {d} = useGetQuery();
  const {data} = useSelector(state => state);
  const {id=0, pid} = useGetCurrent(d);
  if(!data.length)  return [];
  const menuData = data.filter(item => item.folder);

  const children = menuData.reduce((acc, item) => {
    if(item.pid === id){
      item.open = false;
      acc.push(item);
    }
    return acc;
  }, [])
  return [...children, ...getChildren(id, pid, menuData)];
}

const useGetParents = () => {
  const {d} = useGetQuery();
  const {data} = useSelector(state => state);
  let current = useGetCurrent(d);

  return getParents(current, data);
}

const getParents = (file, data) => {
  let current = file;
  let parents = [];
  while (current && current.pid) {
    current = current.pid ? data.find(item => item.folder && item.id === current.pid) : {};
    parents.push({id: current.id, name: current.name});
  }
  return parents;
}

const getChildren = (id, pid, menuData) => {
  let menu = [];
  while (pid !== undefined) {
    let tmp = menuData.filter(item => {
      if (pid === item.pid) {
        item.open = item.id === id;
        return item;
      }
    })
    menu.push(...tmp);
    let next = menuData.find(item => item.id === pid);
    [pid, id] = next ? [next.pid, next.id]: [];
  }
  return menu;
}

const useGetQuery = () => {
  const {location: {search}} = useHistory();
  return search ? qs.parse(search.slice(1)) : {};
}

const useGetCurrent = (sid) => {
  const {data} = useSelector(state => state);
  if(!sid || !data.length)  return {};
  return data.find(item=>item.id.endsWith(sid));
}

const useShowMessage = () => {
  return (msg, success=true) => {
    success ? message.success(msg) : message.error(msg);
  }
}

const useShowModalCreate = () => {
  const dispatch = useDispatch();
  return () => {
    const modal = {
      title: '新建文件夹',
      visible: true,
      width: '40%',
      okText: '提交',
      cancelText: '取消'
    };
    setModal(dispatch, modal, 'create');
  }
}

const useShowModalUpload = () => {
  const dispatch = useDispatch();
  return () => {
    const modal = {
      footer: null,
      title: '文件上传',
      visible: true,
      width: '60%'
    };
    setModal(dispatch, modal, 'upload');
  }
}

const useHideModal = () => {
  const dispatch = useDispatch();
  const modal = useSelector(state=>state.modal);
  return () => {
    setModal(dispatch, {...modal, visible: false});
  }
}

const useSetModal = () => {
  const dispatch = useDispatch();
  const {modal, modalType} = useSelector(state=>state);
  return (option) => {
    setModal(dispatch, {...modal, ...option}, modalType);
  }
}

const setModal = (dispatch, modal, modalType='') => {
  dispatch({
    type: 'SET_MODAL',
    modal,
    modalType
  });

}

export {
  useGetFiles,
  useGetMenu,
  useGetParents,
  useGetQuery,
  useGetCurrent,
  useShowMessage,
  useShowModalCreate,
  useShowModalUpload,
  useSetModal,
  useHideModal
}
