import React, {useRef} from 'react';
import {Layout, Button, Row, Col, Modal} from "antd";
import Container from "./container";
import logo from '../../img/logo.png';
import ShowModal from "../modal";
import {useCreateFolder, useDeleteItem} from "../../store/action";
import {ExclamationCircleOutlined} from "@ant-design/icons";
import {useHideModal, useShowModalCreate, useShowModalUpload} from "../../hooks";

const {Header} = Layout;
const {confirm} = Modal;

function HeaderNav() {
  const deleteItem = useDeleteItem();
  const createFolder = useCreateFolder();
  const showModalUpload = useShowModalUpload();
  const showModalCreate = useShowModalCreate();
  const hideModal = useHideModal();

  const handleOk = () => {
    const {state: {value}} = folder.current;
    createFolder(value);
  }

  const folder = useRef();
  const handleCancel = () => {
    hideModal();
  }

  const handleDelete = (resolve, reject) => {
    deleteItem(resolve, reject);
  }

  const showDeleteConfirm = () => {
    confirm({
      title: '确定要删除选中的文件或文件夹?',
      icon: <ExclamationCircleOutlined/>,
      okText: '删除',
      okType: 'danger',
      cancelText: '取消',
      onOk() {
        return new Promise((resolve, reject) => {
          handleDelete(resolve, reject)
        }).catch(() => console.log('出错!'))
      }
    });
  }

  return <Header theme="light" style={{position: "fixed", zIndex: 99, width: '100%'}}>
    <Container>
      <Row>
        <Col span={4}>
          <img className='logo' src={logo} title='九度云盘' alt='九度云盘'/>
        </Col>
        <Col span={12}/>
        <Col span={8} className='btn-group'>
          <Button onClick={showDeleteConfirm} type="text">删除</Button>
          <Button type="text">移动到</Button>
          <Button type="text" onClick={showModalCreate}>新建文件夹</Button>
          <Button type="text" onClick={showModalUpload}>上传文件</Button>
          <ShowModal
            InputRef={folder}
            handleOk={handleOk}
            handleCancel={handleCancel}
          />
        </Col>
      </Row>
    </Container>

    <nav className="top-nav">
    </nav>
  </Header>
}

export default HeaderNav;
